








import { Component, Vue } from 'vue-property-decorator';

let movexSt = 0;
let moveySt = 0;
let moveR = 0;
let moveT = 0;
let presseMove = false;

@Component
export default class ThreeApp extends Vue {
  private mounted() {
    if ('ontouchstart' in document.documentElement) {
      document.addEventListener('touchstart', this.onTouchSt, false);
      document.addEventListener('touchmove', this.onTouchMv, false);
      document.addEventListener('touchend', this.onTouchEd, false);
      document.addEventListener('touchcancel', this.onTouchEd, false);
    } else {
      document.addEventListener('mousedown', this.onMouceSt, false);
      document.addEventListener('mousemove', this.onMouceMv, false);
      document.addEventListener('mouseup', this.onMouceEd, false);
    }
  }

  private beforeDestroy() {
    document.removeEventListener('touchstart', this.onTouchSt);
    document.removeEventListener('touchmove', this.onTouchMv);
    document.removeEventListener('touchend', this.onTouchEd);
    document.removeEventListener('touchcancel', this.onTouchEd);
    document.removeEventListener('mousedown', this.onMouceSt, false);
    document.removeEventListener('mousemove', this.onMouceMv, false);
    document.removeEventListener('mouseup', this.onMouceEd, false);
  }

  private onTouchSt(e: TouchEvent) {
    const stickEl = this.$el.querySelector('.joystick__stick') as HTMLElement;
    for (let idx = 0; idx < e.touches.length; idx += 1) {
      if (e.touches[idx]?.target === stickEl) {
        movexSt = e.touches[idx].screenX;
        moveySt = e.touches[idx].screenY;
        stickEl.style.transform = `translate(calc(-50% + ${moveR}px), calc(-50% + ${moveT}px)`;
        if (!presseMove) {
          presseMove = true;
          requestAnimationFrame(this.move);
        }
      }
    }
  }

  private onTouchMv(e: TouchEvent) {
    if (presseMove) {
      const stickEl = this.$el.querySelector('.joystick__stick') as HTMLElement;
      for (let idx = 0; idx < e.touches.length; idx += 1) {
        if (e.touches[idx]?.target === stickEl) {
          const X = e.touches[idx].screenX - movexSt;
          const Y = e.touches[idx].screenY - moveySt;
          let a = Math.sqrt(X * X + Y * Y);
          a = (a > 50) ? 50 / a : 1;
          moveR = X * a;
          moveT = Y * a;
          stickEl.style.transform = `translate(calc(-50% + ${moveR}px), calc(-50% + ${moveT}px)`;
        }
      }
    }
  }

  private onTouchEd(e: TouchEvent) {
    if (presseMove) {
      const stickEl = this.$el.querySelector('.joystick__stick') as HTMLElement;
      let mvTF = false;
      for (let idx = 0; idx < e.touches.length; idx += 1) {
        if (e.touches[idx]?.target === stickEl) {
          mvTF = true;
        }
      }
      if (!mvTF) {
        presseMove = false;
        moveR = 0;
        moveT = 0;
        stickEl.style.transform = `translate(calc(-50% + ${moveR}px), calc(-50% + ${moveT}px)`;
      }
    }
  }

  private onMouceSt(e: MouseEvent) {
    const stickEl = this.$el.querySelector('.joystick__stick') as HTMLElement;
    if (e.target === stickEl) {
      movexSt = e.screenX;
      moveySt = e.screenY;
      stickEl.style.transform = `translate(calc(-50% + ${moveR}px), calc(-50% + ${moveT}px)`;
      if (!presseMove) {
        presseMove = true;
        requestAnimationFrame(this.move);
      }
    }
  }

  private onMouceMv(e: MouseEvent) {
    const stickEl = this.$el.querySelector('.joystick__stick') as HTMLElement;
    if (presseMove) {
      const X = e.screenX - movexSt;
      const Y = e.screenY - moveySt;
      let a = Math.sqrt(X * X + Y * Y);
      a = (a > 50) ? 50 / a : 1;
      moveR = X * a;
      moveT = Y * a;
      stickEl.style.transform = `translate(calc(-50% + ${moveR}px), calc(-50% + ${moveT}px)`;
    }
  }

  private onMouceEd() {
    const stickEl = this.$el.querySelector('.joystick__stick') as HTMLElement;
    if (presseMove) {
      presseMove = false;
      moveR = 0;
      moveT = 0;
      stickEl.style.transform = `translate(calc(-50% + ${moveR}px), calc(-50% + ${moveT}px)`;
    }
  }

  private move() {
    if (presseMove) {
      this.$emit('onMove', [moveR, moveT]);
      requestAnimationFrame(this.move);
    }
  }
}
