




import {
  Component,
  Prop,
  Vue,
  Watch,
} from 'vue-property-decorator';
import axios from 'axios';
export const positions = [
  {
    x: -20,
    y: 30,
  },
  {
    x: 20,
    y: 10,
  },
  {
    x: 30,
    y: -30,
  },
  {
    x: 0,
    y: 0,
  },
]

interface IMapObj {
  type: string;
  contentF: string;
  contentB: string;
  element: HTMLElement;
  innerElements: Array<HTMLImageElement | HTMLElement>;
  state: {
    x: number;
    y: number;
    z: number;
    thetaXY: number;
    thetaZ: number;
    width: number;
    height: number;
  };
}

@Component({
  components: {},
})
export default class HtmlControl extends Vue {
  private map = {
    xMax: 300,
    yMax: 300,
  };

  @Prop() private state!: {
    x: number;
    y: number;
    z: number;
    thetaZ: number;
    thetaXY: number;
  };

  private moving = false;

  private init = false;

  private minimapSize = 0;

  private screenSize = [0, 0];

  private screenRatio = 0;

  private objects: IMapObj[] = [
    // {
    //   type: 'img',
    //   element: document.createElement('div'),
    //   innerElements: [],
    //   contentF: 'https://dororodoroddo.github.io/img/dog.png',
    //   contentB: 'https://dororodoroddo.github.io/img/back.jpg',
    //   state: {
    //     x: positions[0].x,
    //     y: positions[0].y,
    //     z: 2,
    //     thetaXY: 0,
    //     thetaZ: 0,
    //     width: 1,
    //     height: 1,
    //   },
    // },
    // {
    //   type: 'img',
    //   element: document.createElement('div'),
    //   innerElements: [],
    //   contentF: 'https://dororodoroddo.github.io/img/cat_transparent.png',
    //   contentB: '',
    //   state: {
    //     x: positions[1].x,
    //     y: positions[1].y,
    //     z: 2,
    //     thetaXY: 10,
    //     thetaZ: 90,
    //     width: 1,
    //     height: 1,
    //   },
    // },
    // {
    //   type: 'img',
    //   element: document.createElement('div'),
    //   innerElements: [],
    //   contentF: 'https://dororodoroddo.github.io/img/dot_transparent.png',
    //   contentB: '',
    //   state: {
    //     x: positions[2].x,
    //     y: positions[2].y,
    //     z: 2,
    //     thetaXY: 10,
    //     thetaZ: 0,
    //     width: 1,
    //     height: 1,
    //   },
    // },
    {
      type: 'html',
      element: document.createElement('div'),
      innerElements: [],
      contentF: '',
      contentB: '',
      state: {
        x: positions[3].x,
        y: positions[3].y,
        z: 2,
        thetaXY: 20,
        thetaZ: 0,
        width: 1,
        height: 1,
      },
    },
  ];

  private onResize() {
    this.screenSize = [window.innerWidth, window.innerHeight];
    this.screenRatio = this.screenSize[1] / this.screenSize[0];
  }

  private mounted() {
    this.onResize();
    document.addEventListener('resize', this.onResize);

    // 초기 오브젝트 로드
    this.objects.forEach((it) => {
      const item = it;
      let itemF: HTMLImageElement | HTMLElement | null = null;
      if (item.type === 'img') {
        item.element.className = 'img-container';
        itemF = document.createElement('img') as HTMLImageElement;
        (itemF as HTMLImageElement).src = item.contentF;
        itemF.className = 'img-front';
        item.innerElements.push(itemF);
        if (item.contentB !== '') {
          const itemImgB = document.createElement('img') as HTMLImageElement;
          itemImgB.src = item.contentB;
          itemImgB.style.width = '1px';
          itemImgB.style.height = '1px';
          itemImgB.style.backfaceVisibility = 'hidden';
          itemImgB.style.webkitBackfaceVisibility = 'hidden';
          item.element.appendChild(itemImgB);
          item.innerElements.push(itemImgB);
        } else {
          itemF.style.backfaceVisibility = 'visible';
          item.innerElements = [itemF];
        }
      } else if (item.type === 'html') {
        item.element.className = 'html-container';
        itemF = document.createElement('div') as HTMLElement;
        const itemB = document.createElement('div') as HTMLElement;
        itemF.className = 'html-item';
        itemB.className = 'html-inner'
        axios.get('https://dororodoroddo.github.io/notion.html')
          .then((res)=>{
            let data = res.data as string;
            data = data.split('<body>')[1];
            data = data.split('</body>')[0];
            itemB.innerHTML = data;
            itemF!.appendChild(itemB);
          })
        item.innerElements.push(itemF);
        item.innerElements.push(itemB);
      }
      if (itemF) {
        item.element.appendChild(itemF);
      }
      this.$el.appendChild(item.element);
    });
    this.moveObjects();
  }

  @Watch('state')
  private moveObjects() {
    const pi = Math.PI;
    const halfFov = [0, (5 / 24) * pi];
    halfFov[0] = halfFov[1] * (this.screenSize[0] / this.screenSize[1]);
    
    this.objects.forEach((it) => {
      const item = it;
      const {
        relX, relY, relAngleZ, relAngleXY,
      } = this.getRelativeProperty(
        item.state.x,
        item.state.y,
        item.state.z,
      );
      // 특정 각도 미만에서 이미지 로드
      if (Math.abs(relAngleZ) < 1.4) {
        if (item.element.style.opacity === '0') {
          item.element.style.opacity = '1';
        }
        const r = Math.sqrt(relX * relX + relY * relY);
        const w = ((2 * Math.atan(item.state.width / 2 / r)) / halfFov[0]) * this.screenSize[0];
        const h = ((2 * Math.atan(item.state.height / 2 / r)) / halfFov[0]) * this.screenSize[0];
        // const w = 100;
        // const h = 100;
        // const dirX = 0;
        // const dirY = 0;
        const dirX = (item.state.thetaXY * pi) / 180;
        const dirY = (item.state.thetaZ * pi) / 180 - Math.atan2(-relY, -relX) - pi;
        const unit = this.screenSize[0] / 2 / halfFov[0];
        const transX = relAngleZ * unit;
        item.element.style.transform = `translate(${this.screenSize[0] / 2 - transX}px, ${-(this.screenSize[1] / 2 - (relAngleXY * halfFov[1] * this.screenSize[1]))}px)`;
        if (item.type === 'img') {
          item.innerElements[0].style.transform = `translate(-50%, -50%) scaleX(${w}) scaleY(${h}) perspective(${r}px) rotateY(${dirY}rad) rotateX(${dirX}rad)`;
          if (item.innerElements.length === 2) {
            item.innerElements[1].style.transform = `scaleX(${w}) scaleY(${h}) perspective(${r}px) rotateY(${
              dirY + Math.PI
            }rad) rotateX(${-dirX}rad)`;
          }
        } else if (item.type === 'html') {
          console.log(this.screenSize[1] * r);
          
          item.innerElements[0].style.transform = `translate(-50%, -50%) scaleX(${w/this.screenSize[0]}) scaleY(${h/this.screenSize[1]})`;
          item.innerElements[1].style.transform = `translate(-50%, 0) perspective(${this.screenSize[1] * r}px) rotateY(${dirY}rad) rotateX(${dirX}rad)`;
          // item.element.style.width = `${w}px`;
          // item.element.style.height = `${h}px`;
          // item.innerElements[0].style.transform = `perspective(${this.screenSize[1] * r}px) rotateY(${dirY}rad)`;
        }
        item.element.style.zIndex = `${
          this.map.xMax + this.map.yMax - Math.floor(r)
        }`;
      } else if (item.element.style.opacity !== '0') {
        item.element.style.opacity = '0';
      }
    });
  }

  private getRelativeProperty(x: number, y: number, z: number) {
    // 상대 좌표, 각도 계산
    const relX = x - this.state.x;
    const relY = y - this.state.y;
    const relZ = z - this.state.z;
    let relAngleZ = Math.atan2(relY, relX) - this.state.thetaZ;
    // relAngleXY ;
    // 계산하기 편하게 변환
    while (relAngleZ < -Math.PI || relAngleZ > Math.PI) {
      if (relAngleZ < -Math.PI) {
        relAngleZ += 2 * Math.PI;
      } else {
        relAngleZ -= 2 * Math.PI;
      }
    }
    let relAngleXY = -Math.atan2(relZ, Math.sqrt(relX * relX + relY * relY));
    while (relAngleXY < -Math.PI || relAngleXY > Math.PI) {
      if (relAngleXY < -Math.PI) {
        relAngleXY += 2 * Math.PI;
      } else {
        relAngleXY -= 2 * Math.PI;
      }
    }
    return {
      relX, relY, relZ, relAngleZ, relAngleXY,
    };
  }
}
